@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Poppins",sans-serif;background-color:#f5f5f5;color:#000}.employeeCard-container{display:flex;flex-wrap:wrap;grid-gap:20px;gap:20px;justify-content:center}
.employeeCard{display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:15px;width:250px;height:300px;background-color:#2a9d8f;box-shadow:5px 10px 10px #888}.employeeCard h2{background-color:#2a9d8f}.employeeCard h4{background-color:#2a9d8f}.employeeCard__counter{background-color:#e9c46a}
.container{border-radius:20px;display:flex;flex-direction:column;align-items:center;margin-bottom:15px;background-color:#e9c46a;box-shadow:5px 5px 20px #5c5c5c}.container h4{background-color:#e9c46a}.container .counter{display:flex;align-items:center;grid-gap:20px;gap:20px;padding:10px;background-color:#e9c46a}.container .counter__number{background-color:#e9c46a}.container .counter__btn{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background-color:#f4a261;border:none;border-radius:10px;box-shadow:5px 5px 5px #5c5c5c;cursor:pointer;font-weight:500;font-size:18px}
.header{text-align:center;margin-bottom:50px;color:#000}
